.add_container {
  position: relative;
  display: flex;
  margin: 0 auto;
  flex-direction: column;
  width: 50%;
}

.add_container input {
  width: 90%;
}

.add_container button {
  width: 10%;
  margin: 0 auto;
}

.add_container .buttonImportant {
  width: 10%;
  position: absolute;
  top: 0;
  right: 0;
}

.buttonAdd {
  color: green;
}

.dateContainer {
  display: flex;
  flex-grow: unset;
}
.dateContainer input {
  width: 50%;
}
