.blurWrapper {
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  visibility: hidden;
}

.formWrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 35%;
  background-color: #eee;
}

.show {
  visibility: visible;
}

.quit {
  position: absolute;
  top: 0;
  right: 0;
}

.formWrapper form {
  display: flex;
  flex-direction: column;
}

.formWrapper input {
  width: 90%;
}
.formWrapper button {
  width: 10%;
  margin: 0 auto;
}
